.main {
  padding: 130px 0 40px;
  &__desc {
    position: relative;
    z-index: 3;
    max-width: 760px;
  }
  &__title {
    font-size: 58px;
    line-height: 120%;
    font-weight: 500;
    letter-spacing: -1.16px;
    color: #0C0C3B;
    span {
      color: #FF7F25;
    }
  }
  &__text {
    max-width: 585px;
    margin-top: 36px;
    p {
      font-size: 16px;
      line-height: 150%;
      letter-spacing: -0.32px;
      color: #646464;
    }
  }
  &__btn {
    height: 60px;
    gap: 24px;
    border-radius: 195px;
    padding: 5px 10px 5px 40px;
    margin-top: 36px;
    span {
      font-size: 18px;
      letter-spacing: -0.36px;
    }
    i {
      width: 42px;
      height: 42px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FF7F25;
      border-radius: 50%;
      svg {
        max-width: 80%;
        max-height: 80%;
      }
    }
  }
  &__bottom {
    position: relative;
    margin-top: 36px;
  }
  &__bottom-wrap {
    position: relative;
    width: 100%;
    height: 100%;
    background: #FAFAFB;
    border-radius: 20px;
    padding: 45px 45px 180px 45px;
    overflow: hidden;
  }
  &__feedback {
    position: relative;
    z-index: 2;
    display: inline-flex;
    align-items: center;
    gap: 16px;
    background: #fff;
    border-radius: 69px;
    padding: 8px 28px 8px 8px;
    &-images {
      display: flex;
      align-items: center;
    }
    .user-img {
      width: 60px;
      &:not(:first-child) {
        margin-left: -25px;
      }
      img {
        width: 100%;
        border: 3px solid #fff;
        border-radius: 50%;
      }
    }
    &-text {
      display: flex;
      align-items: center;
      gap: 8px;
      i {
        width: 22px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 30px;
        line-height: 120%;
        font-weight: 500;
        letter-spacing: -0.74px;
        color: #0C0C3B;
      }
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    right: -130px;
    bottom: 40px;
    max-width: 876px;
    img {
      width: 100%;
    }
  }
  .circle {
    position: absolute;
    left: -300px;
    bottom: -235px;
    width: 470px;
    height: 470px;
    display: block;
    background: #FF7F25;
    border-radius: 50%;
    filter: blur(250px);
    -webkit-filter: blur(250px);
    -moz-filter: blur(250px);
    -ms-filter: blur(250px);
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    right: -300px;
    bottom: -235px;
    width: 470px;
    height: 470px;
    display: block;
    background: #0C0C3B;
    border-radius: 50%;
    filter: blur(250px);
    -webkit-filter: blur(250px);
    -moz-filter: blur(250px);
    -ms-filter: blur(250px);
    pointer-events: none;
  }
}
.partners {
  padding: 40px 0;
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
  }
  &__item {
    max-width: 190px;
    img {
      max-width: 100%;
    }
  }
}
.info {
  padding: 40px 0;
  &__title {
    max-width: 865px;
    text-align: center;
    margin: 0 auto;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 16px / 2));
    gap: 16px;
    margin-top: 50px;
  }
  &__item {
    background: rgba(255, 255, 255, 0.16);
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 99.3px 0px rgba(0, 0, 0, 0.02);
    border-radius: 15px;
    padding: 16px;
    &--100 {
      grid-column: 1/3;
    }
  }
}
.analysis {
  &__title {
    font-size: 20px;
    line-height: 150%;
    letter-spacing: -0.4px;
    color: #212121;
  }
  &__subitems {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 7px 14px;
    margin-top: 3px;
  }
  &__subitem {
    display: flex;
    align-items: center;
    gap: 4px;
    &-icon {
      max-width: 14px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 11px;
      line-height: 120%;
      letter-spacing: -0.22px;
      color: #888888;
    }
  }
  .percent {
    position: absolute;
    z-index: 2;
    border-radius: 7px;
    padding: 4px 8px;
    font-size: 11px;
    letter-spacing: -0.22px;
    color: #222222;
    span {
      color: #666666;
    }
  }
  &__percent {
    top: 28px;
    left: -68px;
  }
  &__percent2 {
    right: -50px;
    bottom: 28px;
  }
  &-statistics {
    margin-top: 20px;
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 8px;
    }
  }
  &-statistics__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    &-left {
      width: 80px;
      flex-shrink: 0;
    }
    &-right {
      max-width: 330px;
      width: 1000%;
      display: flex;
      align-items: center;
    }
    &-title {
      width: 80px;
      font-size: 14px;
      line-height: 150%;
      letter-spacing: -0.28px;
    }
  }
  &-statistics__percent {
    width: 40px;
    flex-shrink: 0;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: -0.24px;
    margin-right: 5px;
  }
  &-statistics__line {
    position: relative;
    max-width: 220px;
    width: 100%;
    height: 9px;
    span:nth-child(1) {
      width: 100%;
      height: 100%;
      display: block;
      background: #CCCCCC;
      border-radius: 2px;
    }
    span:nth-child(2) {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      background: #FF7F25;
      border-radius: 2px;
    }
  }
  &-statistics__indicators {
    width: 55px;
    flex-shrink: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 4px;
    margin-left: 5px;
    i {
      width: 7px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 12px;
      line-height: 150%;
      letter-spacing: -0.24px;
      color: #F71A1A;
    }
  }
  &__img {
    position: absolute;
    top: -50px;
    right: -80px;
    width: 97px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    left: -80px;
    bottom: -100px;
    width: 89px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.traffic {
  &-statistics {
    margin-top: 20px;
  }
  &-statistics__items {
    display: grid;
    grid-template-columns: 100%;
  }
  &-statistics__item {
    position: relative;
    height: 37px;
    &:last-child {
      height: auto;
    }
    &:after {
      content: '';
      position: absolute;
      top: 10px;
      right: 0;
      width: calc(100% - 30px);
      height: 0.5px;
      background: #DDDDDD;
      pointer-events: none;
    }
    &-title {
      position: relative;
      width: 30px;
      span {
        display: block;
        font-size: 14px;
        line-height: 150%;
        letter-spacing: -0.28px;
        color: #212121;
      }
    }
  }
  &-statistics__socials {
    display: flex;
    justify-content: space-between;
    gap: 8px;
    padding-left: 67px;
    margin-top: 8px;
  }
  &-statistics__social {
    position: relative;
    &-title {
      text-align: center;
      span {
        font-size: 16px;
        line-height: 150%;
        letter-spacing: -0.32px;
        color: #212121;
      }
    }
  }
  &-statistics__percent-group {
    position: absolute;
    top: -166px;
    left: 50%;
    transform: translate(-50%, 0);
    height: 148px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
  }
  &-statistics__percent {
    position: relative;
    z-index: 1;
    background: #fff;
    padding: 1px 2px;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: -0.24px;
    color: rgba(33, 33, 33, 0.7);
  }
  &-statistics__percent-diagram {
    width: 42px;
    min-height: 2px;
    background: #FF7F25;
    border-radius: 4px 4px 0 0;
  }
}
.brand {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__top {
    margin-bottom: auto;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    margin-top: 20px;
  }
  &__item {
    position: relative;
    height: 42px;
    display: flex;
    align-items: center;
    background: rgba(33, 33, 33, 0.1);
    border-radius: 8px;
    padding: 8px;
  }
  &__percent-diagram {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background: #FF7F25;
    border-radius: 8px;
  }
  &__percent-text {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 6px;
    padding: 2px 8px;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: -0.24px;
    color: #212121;
    span {
      color: rgba(33, 33, 33, 0.7);
    }
  }
}
.info2 {
  padding: 40px 0 60px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 40px;
  }
  &__left {
    width: 49%;
  }
  &__right {
    position: relative;
