      }
      p {
        font-size: 16px;
        line-height: 150%;
        letter-spacing: -0.32px;
      }
      p:nth-child(1) {
        width: 35%;
        color: #0C0C3B;
      }
      p:nth-child(2) {
        width: 65%;
        color: #646464;
      }
    }
  }
  &__img {
    position: relative;
    height: 100%;
    padding-bottom: 60%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 30px;
    }
  }
  &__btn {
    position: absolute;
    left: 50%;
    bottom: -25px;
    transform: translate(-50%, 0);
    width: max-content;
    height: 60px;
    gap: 24px;
    border-radius: 195px;
    padding: 5px 10px 5px 40px;
    margin-top: 36px;
