      align-items: center;
      background: #FF7F25;
      border-radius: 50%;
      svg {
        max-width: 80%;
        max-height: 80%;
      }
    }
  }
}
.feedback {
  padding: 60px 0 40px;
  &__title {
    max-width: 790px;
