        width: 100%;
      }
    }
    &-text {
      margin-top: 16px;
      p {
        font-size: 16px;
        line-height: 150%;
        letter-spacing: -0.32px;
        color: #0C0C3B;
      }
    }
    &-user {
      display: flex;
      align-items: center;
      gap: 20px;
      margin-top: 32px;
    }
  }
  &__user-img {
    width: 72px;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__user-name {
    font-size: 16px;
    line-height: 150%;
    letter-spacing: -0.32px;
    color: #0C0C3B;
  }
  &__user-date {
    font-size: 14px;
    line-height: 150%;
    letter-spacing: -0.28px;
    color: #5F5F8A;
    margin-top: 6px;
  }
}
