.policy {
  padding: 200px 0 40px;
  &__inner {
    h2 {
      font-size: 44px;
      line-height: 120%;
      font-weight: 500;
      letter-spacing: -0.88px;
      margin-bottom: 12px;
    }
    p {
      font-size: 18px;
      line-height: 188%;
    }
    hr {
      width: 100%;
      height: 30px;
      padding: 0;
      margin: 0;
      opacity: 0;
      pointer-events: none;
    }
    a {
      display: inline-flex;
      align-items: center;
      gap: 10px;
      color: #FF7F25;
      margin-left: 5px;
      i {
        width: 20px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 24px;
        line-height: 140%;
        font-weight: 500;
      }
    }
  }
}

@media screen and (max-width: 1330px) {
  .main {
    &__img {
      right: -100px;
      max-width: 800px;
      width: 61vw;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding-bottom: 30px;
    &__desc {
      max-width: 460px;
    }
    &__title {
      font-size: 32px;
      letter-spacing: -0.64px;
    }
    &__text {
      margin-top: 20px;
    }
    &__btn {
      margin-top: 20px;
    }
    &__bottom {
      &-wrap {
        padding: 30px 30px 120px 30px;
      }
    }
    &__feedback {
      .user-img {
        width: 50px;
        &:not(:first-child) {
          margin-left: -20px;
        }
      }
      &-text {
        span {
          font-size: 24px;
        }
      }
    }
    &__img {
      right: -50px;
    }
  }
  .partners {
    padding: 30px 0;
  }
  .info {
    padding: 30px 0;
    &__title {
      max-width: 550px;
    }
    &__items {
      grid-template-columns: 100%;
      margin-top: 30px;
    }
    &__item {
      &--100 {
        grid-column: 1;
      }
    }
  }
  .info2 {
    padding: 30px 0 45px;
    &__inner {
      gap: 20px;
    }
    @media screen and (min-width: 769px) {
      &__list {
        li {
          p {
            font-size: 14px;
            letter-spacing: -0.28px;
          }
        }
      }
    }
  }
  .feedback {
    padding: 45px 0 30px;
    &__title {
      max-width: 500px;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
      margin-top: 30px;
    }
  }
  .services {
    padding: 30px 0;
    &__inner {
      padding: 30px;
    }
    &__desc {
      gap: 16px;
    }
    &__items {
      flex-direction: column;
      gap: 16px;
    }
  }
  .policy {
    padding: 160px 0 30px;
    &__inner {
      h2 {
        font-size: 26px;
      }
      p {
        font-size: 16px;
      }
      hr {
        height: 20px;
      }
      a {
        span {
          font-size: 18px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .partners {
    &__items {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 64px / 3));
    }
  }
  .analysis {
    &__title {
      font-size: 18px;
    }
    &-statistics__item-title {
      font-size: 12px;
      letter-spacing: -0.24px;
    }
  }
  .traffic {
    &-statistics__item {
      &-title {
        span {
          font-size: 12px;
          letter-spacing: -0.24px;
        }
      }
    }
    &-statistics__socials {
      padding-left: 48px;
    }
    &-statistics__social {
      &-title {
        span {
          font-size: 12px;
          letter-spacing: -0.24px;
        }
      }
    }
    &-statistics__percent-group {
      top: -163px;
    }
    &-statistics__percent {
      font-size: 10px;
      letter-spacing: -0.2px;
    }
    &-statistics__percent-diagram {
      width: 22px;
    }
  }
  .info2 {
    &__inner {
      flex-direction: column;
      gap: 30px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      max-width: 500px;
      margin: 0 auto;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding: 100px 0 0 0;
    &__bottom {
      background: #FAFAFB;
      border-radius: 20px;
      padding: 30px;
      overflow: hidden;
      &-wrap {
        position: static;
        border-radius: 0;
        padding: 0;
      }
    }
    &__img {
      position: relative;
      z-index: 1;
      right: unset;
      bottom: unset;
      width: 90%;
      margin: 20px auto 0;
    }
  }
  .traffic {
    &-statistics__social {
      &-title {
        span {
          font-size: 8px;
          letter-spacing: -0.16px;
        }
      }
    }
    &-statistics__socials {
      padding-left: 36px;
    }
    &-statistics__percent {
      font-size: 8px;
      letter-spacing: -0.16px;
    }
    &-statistics__percent-diagram {
      width: 16px;
    }
  }
  .info2 {
    &__img {
      min-height: 230px;
      img {
        border-radius: 16px;
      }
    }
  }
  .feedback {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .policy {
    padding-top: 120px;
  }
}
