.wrapper {
  overflow: hidden;
}
.btn-orange {
  background: #FF7F25;
  &:hover {
    background: #ff6b08;
  }
  span {
    line-height: 150%;
    font-weight: 500;
    color: #fff;
  }
}
.btn-blue {
  background: #0C0C3B;
  &:hover {
    background: #08085c;
  }
  span {
    line-height: 150%;
    color: #fff;
  }
}
.h2 {
  font-size: 44px;
  line-height: 120%;
  font-weight: 500;
  letter-spacing: -0.88px;
  color: #0C0C3B;
  span {
    color: #FF7F25;
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 26px;
    letter-spacing: -0.52px;
  }
}