.header {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
    padding: 25px 0;
  }
  &__logo {
    width: 175px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__menu {
    display: flex;
    align-items: center;
    gap: 50px;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__btn {
    height: 48px;
    border-radius: 176px;
    padding: 4px 32px;
    span {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
  }
}
.header-fixed {
  box-shadow: 0px 4px 8px 0px rgba(34, 60, 80, 0.2);
}
.header2 {
  background: transparent;
  box-shadow: unset !important;
  -webkit-box-shadow: unset !important;
  -moz-box-shadow: unset !important;
  .header__inner {
    background: #F6F7F9;
    padding: 25px 30px;
    border-radius: 15px;
  }
}
.menu {
  li a {
    font-size: 16px;
    line-height: 150%;
    letter-spacing: -0.32px;
    color: #646464;
    &:hover {
      color: #0C0C3B;
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 32px;
  height: 20px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background: #FF7F25;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: #fff;
    }
    span:nth-child(1) {
      top: 9px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 8px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1100px) {
  .header {
    &__inner {
      gap: 32px;
    }
    &__menu {
      gap: 32px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__inner {
      gap: 16px;
    }
    &__menu {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      background: #FF7F25;
      padding: 150px 15px 50px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &__menu {
      li a {
        color: #fff;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__inner {
      padding: 16px 0;
    }
    &__logo {
      width: 140px;
    }
    &__btn {
      padding: 4px 24px;
      span {
        font-size: 14px;
        letter-spacing: -0.28px;
      }
    }
  }
  .header2 {
    background: #F6F7F9;
    border-radius: 15px;
    .header__inner {
      border-radius: 0;
      padding: 16px 0;
    }
  }
}

@media screen and (max-width: 399px) {
  .header {
    &__inner {
      gap: 8px;
    }
    &__logo {
      width: 120px;
    }
    &__group {
      gap: 8px;
    }
    &__btn {
      height: 40px;
      padding: 4px 16px;
      span {
        font-size: 12px;
      }
    }
  }
}