.footer {
  padding-top: 40px;
  &-top {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    padding-bottom: 30px;
  }
  &__title {
    max-width: 315px;
    margin-bottom: 16px;
  }
  &__text {
    font-size: 14px;
    line-height: 150%;
    letter-spacing: -0.28px;
    color: #646464;
    a {
      color: #0C0C3B;
      text-decoration: underline;
      &:hover {
        text-decoration: unset;
      }
    }
  }
  &__subtitle {
    font-size: 16px;
    line-height: 150%;
    font-weight: 500;
    letter-spacing: -0.32px;
    color: #0C0C3B;
    margin-bottom: 16px;
  }
  &__form {
    display: flex;
    align-items: stretch;
    gap: 12px;
    margin-bottom: 32px;
    &-group {
      input {
        width: 295px;
        height: 50px;
        background: #F6F7F9;
        border: 1px solid transparent;
        border-radius: 160px;
        padding: 0 24px;
        font-size: 14px;
        line-height: 120%;
        color: #0C0C3B;
        &::placeholder {
          color: #808EA9;
        }
      }
    }
    &-btn {
      height: 50px;
      flex-shrink: 0;
      border-radius: 160px;
      padding: 5px 30px;
      span {
        font-size: 16px;
        letter-spacing: -0.32px;
      }
    }
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    border-top: 1px solid #ECEFF5;
    padding: 30px 0;
  }
  &__logo {
    width: 174px;
    display: block;
    img {
      width: 100%;
    }
  }
  .menu {
    display: flex;
    align-items: center;
    gap: 40px;
  }
}

@media screen and (max-width: 1250px) {
  .footer {
    &-bottom {
      gap: 32px;
    }
    .menu {
      gap: 32px;
      li a {
        font-size: 14px;
        letter-spacing: -0.28px;
      }
    }
  }
}

@media screen and (max-width: 1250px) {
  .footer {
    &-bottom {
      gap: 20px;
    }
    &__logo {
      width: 150px;
    }
    .menu {
      gap: 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding-top: 40px;
    &-top {
      padding-bottom: 30px;
    }
    &-bottom {
      flex-direction: column;
      gap: 30px;
    }
    .menu {
      flex-wrap: wrap;
      justify-content: center;
      gap: 30px;
      li a {
        font-size: 16px;
        letter-spacing: -0.32px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &-top {
      flex-direction: column;
    }
    &__form {
      &-group {
        flex-grow: 1;
        input {
          width: 100%;
          min-width: 150px;
          padding:  0 16px;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__form {
      flex-direction: column;
      &-btn {
        width: 100%;
      }
    }
  }
}